@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500&display=swap');

@font-face {
    font-family: 'FuturaPTBook'; /* Define the name for the font */
    src: url('/public/fonts/FuturaPTBook.otf') format('woff2');
    font-weight: normal;
    font-style: normal;
}

* {
    font-family: 'FuturaPTBook', sans-serif;
}

#footer {
    background-color: rgb(41, 24, 24);
}

#info {
    display: flex;
    flex-direction: row;
    height: fit-content;
}

#footercontent {
    display: flex;
    flex-direction: row;
}

#Logos {
    margin-left: 100px;
    margin-right: 20px;
    margin-bottom: 100px;
}

#Links {
    display: flex;
    flex-direction: column;
    color: white;
}

Link {
    text-decoration: none;
}

#theurllinks {
    display: flex;
    flex-direction: column;
    margin-left: 10px;
}

.linkstyle {
    color: white;
    text-decoration: none;
}

.linkstyle:hover {
    color: green !important;
}

#visitingBeiteddine {
    margin-bottom: 10px;
    font-weight: bold;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-size: larger;
}

#titleOpening {
    margin-bottom: 5px;
    font-weight: bold;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-size: large;
}

#message {
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    margin-bottom: 10px;
}

.openingHours {
    margin-left: 50px;
    margin-right: 50px;
    margin-top: 50px;
    margin-bottom: 20px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
}

#telNumber {
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    color: white;
}

.secondCol {
    display: flex;
    flex-direction: column;
    margin-left: 50px;
    margin-right: 50px;
    margin-bottom: 20px;
}

.entryFees {
    margin-top: 50px;
    margin-bottom: 20px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    color: white;
    background-color: rgb(24, 13, 13);
    padding: 20px;
    height: 300px;
}

#entry {
    font-size: 24px;
    font-weight: bold;
    text-transform: uppercase;
    color: white;
    margin-bottom: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.fees {
    display: grid;
    grid-template-columns: auto auto;
    column-gap: 30px;
    row-gap: 10px;
}

.fee-category {
    grid-column: 1 / -1;
    font-weight: bold;
    margin-top: 10px;
}

.fee-amount {
    display: flex;
    justify-content: space-between;
}

.fee-amount span {
    margin-left: 30px;
}

#allRightsReserved {
    color: white;
}

.location {
    margin-left: 50px;
    margin-right: 0px;
    margin-top: 50px;
    margin-bottom: 20px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;

}

#loc {
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    margin-bottom: 5px;
}

.maps {
    width: 100%;
    height: 350px;
    border: none;
    padding: 0;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}
.footer-signup-textfield input.email, .footer-signup-button input[type="submit"] {
    width: 100%; 
    padding: 10px; 
    border: 2px solid #ffffff; 
    background: rgba(0, 0, 0, 0.5); 
    color: white;
    font-size: 16px; 
    cursor: pointer; 
    margin-top: 2vh;
}
@media (max-width: 768px) {
    #info, #footercontent {
        flex-direction: column;
        align-items: center;
    }

    .openingHours, .secondCol, .location {
        margin-left: 20px;
        margin-right: 20px;
        align-items: center;
        text-align: center;
    }

    .entryFees{
        width: 90%;
        box-sizing: border-box;
        padding-bottom: 300px;
        margin-bottom: 60px;
    }

    .mailingList {
        width: 90%;
        box-sizing: border-box;
    }
    .fees {
        grid-template-columns: 1fr;
    }

    #entry, #visitingBeiteddine {
        font-size: 20px;
    }
    #Logos {
        flex-direction: column;
        align-items: center;
        margin-left: 20px;
        margin-right: 20px;
    }

    #Logos img {
        margin-bottom: 10px;
    }

    #allRightsReserved {
        text-align: center;
        margin-top: 20px;
    }

    .maps {
        height: 300px;
    }

    .location{
        align-items: flex-start;
    }
}