/* ChatbotComponent.css */

.chat-container {
    position: fixed;
    right: 20px;
    bottom: 80px;
    width: 400px;
    height: 600px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
    z-index: 1000;
    display: flex;
    flex-direction: column;
}

.chat-header {
    padding: 20px;
}

.chat-history {
    flex-grow: 1;
    overflow-y: scroll;
    margin-bottom: 20px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #f9f9f9;
}

.user-message {
    text-align: right;
    padding: 10px;
    background-color: #f0f0f0;
    border-radius: 10px;
    margin-bottom: 5px;
}

.bot-message {
    text-align: left;
    padding: 10px;
    background-color: #F3DBB3;
    border-radius: 10px;
    margin-bottom: 5px;
}

.chat-form {
    display: flex;
    padding: 0 20px 20px 20px;
}

.chat-input {
    flex-grow: 1;
    margin-right: 10px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.mute-toggle {
    margin-right: 10px;
    width: 40px;
    height: 40px;
    cursor: pointer;
}

.send-button {
    background-color: #B5964D;
    color: white;
    border: none;
    padding: 10px 15px;
    border-radius: 5px;
    cursor: pointer;
}

.loader {
    text-align: center;
    margin-top: 10px;
}

/* Responsive Styles */
@media (max-width: 600px) {
    .chat-container {
        width: 90%;
        height: 80%;
        right: 5%;
        bottom: 10%;
    }
}