/* Underline Animation */

.titles {
  position: relative;
  display: inline-block;
}

.titles::after { /* ::after psuedo element to start creating the underline */ 
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0; 
  height: 2px; /* Start giving the properties of the undelrine */
  background: black; 
  transition: width 0.5s ease-in-out; /* Here i am defining the transition */
}

.titles:hover::after {
  width: 100%; /* Full width on hover */
}

.whytodo{
  padding-top: 50px;
  margin-top: 40px;
  margin-bottom: 50px;
}

.image-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-gap: 10px;
  padding: 10px;
  margin-bottom: 40px;
  padding-top: 50px;
  padding-bottom: 50px;
}

.grid-image {
  flex: 1; /* Each image will take equal space */
  width: 100%;
  height: auto;
  border-radius: 5px;
}

@media (max-width: 768px) {
  .information {
    flex-direction: column;
  }
  .information img, .container .text-content {
    max-width: 100%; /* Both image and text content take full container width */
}
  .cards-container{
    margin: 0;
  }
  .information-container {
    width: 100%; /* Full width of the viewport */
    margin:0;
    padding: 0;
}
  .information {
    display: flex; /* Continues to use flexbox for layout */
    flex-direction: column; /* Stacks child elements vertically */
    align-items: center; /* Aligns children centrally along the cross axis */
    justify-content: center; /* Centers children along the main axis */
    width: 95%; /* Uses most of the viewport width */
    margin: 20px auto; /* Centers the div horizontally and adds vertical margin */
    padding: 20px; /* Ensures there is padding around the content */
}

  .information img {
      width: 90%;            /* Full width for the image to utilize the available space */
      height: 80%;
      margin-bottom: 20px;    /* Provide separation between the image and the text */
  }

  .information .text {
      width: 100%;            /* Full width for text containers */
      text-align: center;     /* Center text for a clean, readable layout */
  }

  .text0, .text1 {
      width: 100%;            /* Ensure the text containers span the full width of their parent */
      padding: 0 10px; 
      margin:auto; /* Add horizontal padding for better text layout */
  }

  .titles {                  /* Adjust title sizes if necessary */
      font-size: 100px;  
      text-align: center;     /* Slightly smaller font size for headings to fit smaller screens */
  }

  .text0 h2, .text1 h2 {
      font-size: 20px;  
      text-align: center;     /* Reduce the font size of h2 elements for mobile */
  }

  .text0 p, .text p {
      font-size: 16px;       /* Adjust paragraph text size for better readability */
  }

  .text0 {
    text-align: center;  /* Centers the text horizontally within the container */
    padding: 20px;       /* Ensures there's padding around the text for better spacing */
    width: 100%;         /* Ensures the div takes up the full width of its parent */
    display: flex;       /* Uses flexbox to align children */
    flex-direction: column; /* Stacks children vertically */
    justify-content: center; /* Centers content vertically */
    align-items: center; /* Ensures all items are centered horizontally within the flex container */
}

.text0 h2 {
    font-size: 24px; /* Adjust font size as needed for aesthetics */
    margin-top: 10px; /* Spacing before the heading */
    margin-bottom: 10px; /* Spacing after the heading */
}

.text0 h5 {
    font-size: 18px; /* Adjust subtitle font size */
    margin-bottom: 20px; /* Spacing after the subtitle */
}

.text0 p {
    font-size: 16px; /* Paragraph font size for readability */
    line-height: 1.5; /* Line height for better text flow */
}
#card-items {
  display: grid;               /* Using grid layout for better control over column setup */
  grid-template-columns: 1fr 1fr; /* Set up two columns */
  grid-gap: 20px;              /* Space between grid items */
  justify-content: center;     /* Center the grid items horizontally if there's extra space */
  padding: 10px;               /* Padding around the grid container */
}

#card-item {
  width: 100%;                 /* Each card item takes full width of its grid column */
  margin: 0;                   /* Remove any default margins to fit grid styling */
}

.card-item-img {
  width: 100%;                 /* Adjust image width to 100% of its container */
  height: auto;                /* Maintain aspect ratio */
}

#cards-item-info {
  text-align: center;          /* Center the text within each card */
}

