* {
  box-sizing: border-box;
}

.timeline {
   max-width: 1200px;
    position: relative;
    margin: 10px auto; 
    z-index: 1;
}
/* timeline refers to the timeline Line in the middle  */
.timeline::after {
  content: '';
  position: absolute;
  width: 2px;
  background-color: white;
  top: 0;
  bottom: 0;
  left: 50%;
  margin-left: -3px;
}

  .event {
    padding: 10px 40px;
    width:50%; 
    position: relative;
    border-radius: 10px;
  }

/* for the circles */
  .event::after {
    content: '';
    position: absolute;
    width: 25px;
    height: 25px;
    right: -17px;
    background-color: white;
    border: 4px solid #743d09;
    top: 15px;
    border-radius: 50%;
    z-index: 1;
  }

  .event-content {
    text-align: right;
    position: relative;
    background-color: #96673b;
    padding: 10px;
    border-radius: 10px;
  }

  .left {
    left: 0;
  }
  .right {
    left: 50%;
  }
  
 
  /* Fix the circle for containers on the right side */
  .right::after {
    left: -16px;
  }
  
  
  
  
 

