@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@100..900&display=swap');
@font-face {
    font-family: 'FuturaPTBook';
    src: url('/public/fonts/FuturaPTBook.otf') format('woff2'); 
    font-weight: normal; 
    font-style: normal; 
}

* {
    font-family: 'FuturaPTBook', sans-serif;
}

body{
    width: 100%;
    max-width: 100%;
    overflow-x: hidden;  
}
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    background-color: white;
    z-index: 101;
    box-shadow: 2px 2px 3px 2px rgb(0,0,0,0.25);
    position:relative;
}

#imagesid {
    display: flex;
    gap: 10px; 
    padding-top: 5px;
}

#imagesid img {
    border-radius: 5px; 
}
#imagesid2 {
  display: flex;
  gap: 10px; 
  padding-top: 5px;
}

#imagesid2 img {
  border-radius: 5px; 
}


nav .buttons {
    display: flex;
    justify-content: center;
    gap: 20px; 
}

nav .buttons a {
    text-decoration: none;
    color: #333; 
    padding-left: 10px;
    border-radius: 5px;
    transition: background-color 0.3s;
    font-family: "Outfit", sans-serif;
}

nav .buttons a:hover {
    color: rgb(194, 99, 11);
}

.hidden {
    display: none;
}
@media (max-width: 793px) {
  .header{
    height: auto;
    justify-content: space-between;
  }
  #imagesid2, nav .buttons {
      display: none;
  }

  .mobile-menu .buttons {
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding: 10px;
    background-color: white; /* Assuming you want a different background */
}

  .mobile-menu .buttons a {
      text-decoration: none;
      color: #333;
      padding: 10px;
      border-radius: 5px;
      margin-right: 20px;
  }

  .menu-icons button {
      background: none;
      border: none;
      cursor: pointer;
      margin-right: 20px;
  }

  .menu-icons img {
      width: 30px;
      height: 30px;
      margin-right: 20px;
  }
}
/* Ensure that nav buttons are visible when clicked is true */
.clicked nav .buttons {
  display: flex;
  flex-direction: column;
}
#popup-menu {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 0;
  box-shadow: 0 2px 5px rgba(0,0,0,0.2);
  transform: translateY(-100%);
  animation: slideDown 0.3s forwards;
}

@keyframes slideDown {
  from {
      transform: translateY(-100%);
  }
  to {
      transform: translateY(0);
  }
}
.close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: none;
  cursor: pointer;
  margin: 20px;
}

.close-btn img {
  width: 30px;
  height: 30px;
}