@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@100..900&display=swap');
@font-face {
    font-family: 'FuturaPTBook';
    src: url('/public/fonts/FuturaPTBook.otf') format('woff2'); 
    font-weight: normal; 
    font-style: normal; 
}

.history-page {
  text-align: center; /* Center the content */
  padding: 20px; /* Adds padding around the content */
}
.image-gallery {
  display: flex;
  overflow: hidden;
  justify-content: flex-start;
  gap: 20px;
  padding-right: 20px;
  padding-left: 20px;
  padding-bottom: 20px;
}
.image-gallery-inner {
  display: flex;
  transition: transform 0.5s ease-out;
  gap: 20px;
  padding: 20px;
}
.virtual{
  margin: 3vh;
  font-size: 100px;
}
.enjoy{
  margin: 3vh;
  font-weight: none;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 1;
}
  
figure {
  flex: 0 0 calc(100% / 15); 
  margin: 0; /* Removes margin between figures */
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
  border-radius: 8px;
  background-color: #a28761;
}


figure img {
  width: 100%;
  height: auto;
  border-radius: 8px;
}

figcaption {
  color: white;
  font-size: 0.8rem;
  padding-top: 8px;
  padding-bottom: 12px;
}

.frame {
  display: block;
  margin: auto;
  border: none;
  border-radius: 20px;
  box-shadow: 0 6px 20px rgba(0,0,0,0.25);
  max-width: 960px;
  width: 100%;
  height: 570px;
}

@media (max-width: 768px) {
  .virtual{
    font-size: 70px;
  }
  .enjoy{
    font-size: 16px;
  }
  .frame {
    height: 300px;
  }
  figure {
    flex-basis: 4%; 
  
  }
}

#objectsFeatured{
  font-size: 30px;
  margin-bottom: xx-large;
  text-align: start;
  padding-top: 30px;
}

#objectsDescription{
  font-size: 20px;
  text-align: start;
  padding-top: 20px;
}